import React from "react";
import { useState, useEffect } from "react";
import { Fragment, useRef } from "react";
import { Link, useNavigate } from "react-router-dom";
import "./auth.css";
import Loading from "../layout/Loader/Loader";
import { useDispatch, useSelector } from "react-redux";
import { login, register } from "../../actions/userActions";
import { Checkbox } from "@mui/material";
import { genrateOTP_email } from "../../actions/whatsAppAction";
import axios from "axios";

const Auth = (props) => {
  //loops
  const dispatch = useDispatch();
  const navigate = useNavigate();

  //dom elaements
  const switcherTab = useRef(null);
  const loginTab = useRef(null);
  const registerTabs = useRef(null);

  const freeStyle = {
    span: { background: "#111111", color: "white" },
    input: {
      border: "1px solid white",
      background: "transparent",
      color: "white",
    },
    txt: { color: "white" },
  };

  //states
  const [loginEmail, setLoginEmail] = useState("");
  const [loginPassword, setLoginPassword] = useState("");
  const [OTP_state, set_OTP_state] = useState(String);
  const [checkBox, setCheckBox] = useState(false);
  const [otp_Time, set_otp_Time] = useState(120);
  const [msg, set_msg] = useState("Send OTP");
  const [isOtp_input, set_isOtp_input] = useState(false);

  const [userData, setUserData] = useState({
    username: "",
    email: "",
    password: "",
    facultyID: "",
    OTP: "",
    role: "student",
  });

  // data form reduc
  const { loading, error, isAuthenticated } = useSelector(
    (state) => state.user
  );

  //destructured data
  const { username, email, password, facultyID, OTP } = userData;

  //fuctionality
  const switchTabs = (e, Tab) => {
    if (Tab === "login") {
      switcherTab.current.classList.add("shiftToNeutral");
      switcherTab.current.classList.remove("shiftToRight");

      registerTabs.current.classList.remove("shiftToNeutralForm");
      loginTab.current.classList.remove("shiftToLeft");
    }

    if (Tab === "signUp") {
      switcherTab.current.classList.add("shiftToRight");
      switcherTab.current.classList.remove("shiftToNeutral");

      registerTabs.current.classList.add("shiftToNeutralForm");
      loginTab.current.classList.add("shiftToLeft");
    }
  };
  //login
  const LoginSubmit = (e) => {
    e.preventDefault();
    dispatch(login(loginEmail, loginPassword));
  };

  const registerDataChange = (e) => {
    setUserData({ ...userData, [e.target.name]: e.target.value });
  };

  //register
  const registerSubmit = (e) => {
    e.preventDefault();

    if (!checkBox) {
      userData.facultyID = "0000";
    } else {
      if (userData.facultyID === "0000") {
        userData.facultyID = "student";
      }
      userData.role = "teacher";
    }

    if (OTP_state == OTP) {
      dispatch(register(userData));
    } else {
      console.log("envalid otp");
    }
  };

  const startTimer =()=>{    
    const new1  = setInterval(() => {
      set_otp_Time(otp_Time=>otp_Time-1)
    }, 1000);

    setTimeout(() => {
      clearInterval(new1)
     }, 120000);

  }

  const getOTP = async () => {
    const _OTP = Math.round(Math.random() * 1000000);
    set_OTP_state(_OTP);

    const localData = { username: username, email: email, OTP: _OTP };
    set_msg("proccessing");
    await axios
      .post(`/api/v1/user/gerateOTP/email`, localData)
      .then((data) => {
        set_msg(data.data.msg);
        set_isOtp_input(true);
        set_otp_Time(120)
         startTimer()
      })
      .catch((err) => {
        set_msg(err.response.data.msg);
        setTimeout(() => {
          set_msg("Send OTP");
        }, 5000);
      });
  };

  //useEffect loops
  useEffect(() => {
    if (isAuthenticated) {
      navigate("/user/account");
    }
  }, [isAuthenticated]);

  useEffect(() => {
    if (isOtp_input) {
      setTimeout(() => {
        set_isOtp_input(false);
        set_msg("Resend OTP");
        set_otp_Time(120);
      }, 120000);
    }
  }, [isOtp_input]);

  return (
    <Fragment>
      {loading ? (
        <Loading />
      ) : (
        <Fragment>
          <div className="LoginSignUpContainer" style={freeStyle.txt}>
            <div className="LoginSignUpBox">
              <div>
                <div className="login_signUp_toggle">
                  <p onClick={(e) => switchTabs(e, "login")}>LOGIN</p>
                  <p onClick={(e) => switchTabs(e, "signUp")}>SIGNUP</p>
                </div>
                <button ref={switcherTab}></button>
              </div>
              <form className="loginForm" ref={loginTab} onSubmit={LoginSubmit}>
                <label className="custom-field one">
                  <input
                    type="email"
                    required
                    style={freeStyle.input}
                    value={loginEmail}
                    onChange={(e) => setLoginEmail(e.target.value)}
                    id="outlined-basic"
                    label="Email"
                    variant="outlined"
                  />
                  <span class="placeholder " style={freeStyle.span}>
                    Email
                  </span>
                </label>
                <label className="custom-field one">
                  <input
                    type="password"
                    maxLength="12"
                    required
                    style={freeStyle.input}
                    value={loginPassword}
                    onChange={(e) => setLoginPassword(e.target.value)}
                    id="outlined-basic"
                    label="Password"
                    variant="outlined"
                  />
                  <span class="placeholder " style={freeStyle.span}>
                    Password
                  </span>
                </label>
                <Link to="/password/forget" style={freeStyle.txt}>
                  Forget Password?
                </Link>
                <input type="submit" value="Login" className="loginBtn" />
              </form>

              <form
                className="signUpForm"
                ref={registerTabs}
                onSubmit={registerSubmit}
              >
                <label className="custom-field one">
                  <input
                    type="text"
                    required
                    style={freeStyle.input}
                    maxLength="12"
                    value={username}
                    name="username"
                    onChange={registerDataChange}
                    id="outlined-basic"
                    label="Username"
                    variant="outlined"
                  />
                  <span class="placeholder " style={freeStyle.span}>
                    Username
                  </span>
                </label>

                <label className="custom-field one">
                  <input
                    type="email"
                    required
                    style={freeStyle.input}
                    value={email}
                    name="email"
                    onChange={registerDataChange}
                    id="outlined-basic"
                    label="Email"
                    variant="outlined"
                  />
                  <span class="placeholder " style={freeStyle.span}>
                    Email
                  </span>
                </label>
                {
                  <div className="flex_baselineEnd_center">
                    <span
                      onClick={
                        msg === "Send OTP" || "Resend OTP" ? getOTP : null
                      }
                      className="fontLink"
                    >
                      {msg}
                    </span>
                  </div>
                }

                {isOtp_input ? (
                  <label className="custom-field one">
                    <input
                      type="text"
                      required
                      style={freeStyle.input}
                      maxLength="6"
                      value={OTP}
                      name="OTP"
                      onChange={registerDataChange}
                      id="outlined-basic"
                      label="OTP"
                      variant="outlined"
                    />
                    <span class="placeholder " style={freeStyle.span}>
                      OTP (
                       { otp_Time}
                      s)
                    </span>
                  </label>
                ) : null}

                <label className="custom-field one">
                  <input
                    type="text"
                    required
                    style={freeStyle.input}
                    maxLength="12"
                    value={password}
                    name="password"
                    onChange={registerDataChange}
                    id="outlined-basic"
                    label="Password"
                    variant="outlined"
                  />
                  <span class="placeholder " style={freeStyle.span}>
                    Password
                  </span>
                </label>
                <div className="checkBox_lable">
                  <Checkbox
                    type="checkbox"
                    name="check"
                    onChange={(e) => {
                      setCheckBox(e.target.checked);
                    }}
                  />
                  <label htmlFor="check">Register as faculty</label>
                </div>
                {checkBox === true ? (
                  <label className="custom-field one">
                    <input
                      type="text"
                      style={freeStyle.input}
                      required
                      value={facultyID}
                      name="facultyID"
                      onChange={registerDataChange}
                      id="outlined-basic"
                      variant="outlined"
                    />
                    <span class="placeholder " style={freeStyle.span}>
                      Faculty ID
                    </span>
                  </label>
                ) : null}

                <input type="submit" value="Register" className="signUpBtn" />
              </form>
            </div>
          </div>
        </Fragment>
      )}
    </Fragment>
  );
};

export default Auth;
