import {
  CLEAR_ERRORS,
  LOGIN_FAIL,
  LOGIN_REQUEST,
  LOGIN_SUCCESS,
  REGISTER_USER_FAIL,
  REGISTER_USER_REQUEST,
  REGISTER_USER_SUCCESS,
  LOAD_USER_FAIL,
  LOAD_USER_SUCCESS,
  LOAD_USER_REQUEST,
  UPDATE_USER_FAIL,
  UPDATE_USER_SUCCESS,
  UPDATE_USER_REQUEST,
  LOG_OUT,
} from "../constants/userConstants";
import axios from "axios";

export const login = (email, password) => async (dispatch) => {
  try {
    dispatch({ type: LOGIN_REQUEST });

    const config = { Headers: { "Content-Type": "application/json" } };

    const { data } = await axios.post(
      "/api/v1/login",
      { email, password },
      config
    );
    console.log(data.user);
    dispatch({ type: LOGIN_SUCCESS, payload: data.user });
  } catch (error) {
    dispatch({ type: LOGIN_FAIL, payload: error.response.data.msg });
  }
};

export const register = (userData) => async (dispatch) => {
  try {
    dispatch({ type: REGISTER_USER_REQUEST });

    const config = {
      Headers: { "Content-Type": "application/json" },
    };

    const { data } = await axios.post("/api/v1/signup", userData, config);

    dispatch({ type: REGISTER_USER_SUCCESS, payload: data.user });
  } catch (error) {
    console.log(error.response);
    dispatch({ type: REGISTER_USER_FAIL, payload: error.response.data.msg });
  }
};

export const loadUser = () => async (dispatch) => {
  try {
    dispatch({ type: LOAD_USER_REQUEST });

    const { data } = await axios.get("/api/v1/user/me");
    console.log(data);
    dispatch({ type: LOAD_USER_SUCCESS, payload: data.user });
  } catch (error) {
    dispatch({ type: LOAD_USER_FAIL, payload: error.response.data.msg });
  }
};

export const  logOut_user = () => async (dispatch) => {
  try {
    await axios.get("/api/v1/logout");
  } catch (error) {
    dispatch({ type: LOAD_USER_FAIL, payload: error.response.data.msg });
  }
};

export const updateUserBasicDetail = (updated_data) => async (dispatch) => {
  try {
    dispatch({ type: UPDATE_USER_REQUEST });
    const config = { Headers: { "Content-Type": "application/json" } };
    console.log(updated_data);
    const { data } = await axios.put(
      `/api/v1/user/update`,
      updated_data,
      config
    );
    console.log(data);

    if (typeof updated_data.status !== "undefined") {
    } else {
      dispatch({ type: UPDATE_USER_SUCCESS, payload: data.user });
    }
  } catch (error) {
    dispatch({ type: UPDATE_USER_FAIL, payload: error.response.data.msg });
  }
};

//changeing theme

export const change_theme_setting =
  ({ id, theme }) =>
  async (dispatch) => {
    dispatch({ type: UPDATE_USER_REQUEST });
    try {
      let { data } = await axios.put(
        `/api/v1/user/update/setting/${id}/${theme}`
      );
      data.user.settings.theme = theme;
      dispatch({ type: UPDATE_USER_SUCCESS, payload: data.user });
    } catch (error) {
      dispatch({ type: UPDATE_USER_FAIL, payload: error.response.data.msg });
    }
  };

// changing password
export const change_password = (passOBJ, user) => async (dispatch) => {
  dispatch({ type: UPDATE_USER_REQUEST });
  try {
    const { data } = await axios.put(`/api/v1/user/updatePass`, passOBJ);
    console.log(data);
    dispatch({ type: UPDATE_USER_SUCCESS, payload: data.user });
  } catch (error) {
    dispatch({
      type: UPDATE_USER_FAIL,
      payload: error.response.data.msg,
      payload2: user,
    });
  }
};

//checking is user exist or not
export const is_user_exist = (email, OTP) => async (dispatch) => {
  try {
    console.log(email);
    const config = { Headers: { "Content-Type": "application/json" } };
    const { data } = await axios.get(`/api/v1/user/isExist/${email}`,config  );
    if (data.success == true) {
      try {
        await axios.post(`/api/v1/user/gerateOTP/email`, {
          username: data.username,
          email: email,
          OTP: OTP,
        });
      } catch (error) {
        console.log(error);
      }
    }
  } catch (error) {
    console.log( error.response.data);
    dispatch({ type: LOGIN_FAIL, payload: error.response.data.msg });
  }
};

