import React, { useEffect, useState } from "react";
import { Fragment } from "react";
import { useDispatch, useSelector } from "react-redux";
import { get_division_by_data } from "../../../actions/divisionAction";
import {
  add_user_to_mark_attendace,
  delete_created_lecture,
  get_all_recent_lectures,
} from "../../../actions/lectureActions";
import { getUserDetail } from "../../../actions/updateUserAction";
import useGeolocation from "../../../hooks/useGeolocation";
import "./lecture.css";
import Button from '@mui/material/Button';
import ToolsPage from "../toolsPage";
import HeaderComp from "../../layout/HeaderComp/HeaderComp";
import LectureDiv from "../../layout/LectureDiv";
import Note from "../../layout/note/Note"


 

const TodaysLectures = (props) => {

    const [windowSize, setWindowSize] = useState(getWindowSize());

    function getWindowSize() {
        const { innerWidth, innerHeight } = window;
        return { innerWidth, innerHeight };
      }

       //hookes
    const dispatch =useDispatch()


    const { isUserDetailUpdated, userDetail, loading } = useSelector(
        (state) => state.userDetail
      );
      const { 
        division, 
        isDividionGeted, 
    } = useSelector((state) => state.division);

    const { isAllLecturesGained, lectures, isPresentyModified } = useSelector(
        (state) => state.lecture
      );
    
 
      useEffect(() => {
          if (isUserDetailUpdated) { 
        dispatch(get_division_by_data({
                div:userDetail.div,
                 course:userDetail.course,
                  clgShortName:userDetail.clgShortName,
                   year:userDetail.year
        }))
    } 
      }, [isUserDetailUpdated ])
      useEffect(() => {
          if (isDividionGeted) { 
            var start = new Date();
            start.setUTCHours(0,0,0,0);
            
            var end = new Date();
            end.setUTCHours(23,59,59,999);
            
            dispatch(get_all_recent_lectures({
                id:division._id,
                 from : start.getTime(),
                  to: end.getTime()
            }))
    } 
      }, [isDividionGeted ]) 

      useEffect(() => {
        
      }, [])
      

    return (
        <>
        
        {windowSize.innerWidth >= 768?( 
            <div className="userAcountMainDiv " style={props.main.main}> 
    
        <div className="toolsNavigator" >
    
        <ToolsPage></ToolsPage>
        </div>
    
    
    
    
          <div>
            <h1>Today's Lectures</h1>
            <hr />

           
          </div>
        </div>):(
          <div className="mobleDiv4865" style={props.main.main}>  
      <HeaderComp type="Mark Attendance"></HeaderComp>
            <div className="mobileDiv5656 markDiv4564" style={props.main.sub_body}>
              <h1>Today's Lectures</h1>
              <hr />
              <div className="allLectures">

              {isAllLecturesGained?(
                lectures.length==0?(<Note msg="No lecture avalable till now." type="empty"></Note>):(lectures.map(data=>{
                  return(<LectureDiv lectureData={data}></LectureDiv>)
                }))
                ):null
                
              }
              </div>
             
              
            </div>
            <ToolsPage></ToolsPage>
          </div>
        )
    
        }
         </>
      
      );
}

export default TodaysLectures